:root {
	--clr-primary: #ee171f;
	--clr-neutral: #202020;
}

*, *::after, *::before {
	margin: 0;
	box-sizing: border-box;
}

body {
	background: var(--clr-neutral);
	margin: 0;
	font-family: 'Montserrat', sans-serif;;
}

header {
	background: var(--clr-primary);
	text-align: center;
	position: fixed;
	z-index: 999;
	width: 100%;
}

.logo {
	color: white;
	font-size: 2rem;
	height: 1.5em;
}

nav {
	position: absolute;
	text-align: left;
	top: 100%;
	left: 0;
	background: var(--clr-primary);
	width: 100%;
	transform: scale(1, 0);
	transition: transform 250ms ease-in-out;
	transform-origin: top;

	& ul {
		margin: 0;
		padding: 0;
		list-style: none;
	}

	& li {
		margin-bottom: 1em;
		margin-left: 1em;
	}

	& a {
		position: relative;
		color: white;
		text-decoration: none;
		font-size: 1rem;
		font-weight: 800;
		text-transform: uppercase;
		opacity: 0;
		transition: opacity 250ms ease-in-out;
	}

	& a::after {
		content: '';
		position: absolute;
		height: 2px;
		width: 100%;
		left: 0;
		bottom: -.2em; 
		background: currentColor;
		transform: scale(0, 1);
		transition: transform 150ms ease-in-out;
	}

	& a:hover::after {
		transform: scale(1, 1);
	}
}


.nav-toggle {
	display: none;
}

.nav-toggle:checked ~ nav a {
	opacity: 1;
	transition: opacity 250ms ease-in-out 200ms;
}

.nav-toggle:checked ~ nav{
	transform: scale(1, 1);
}

.nav-toggle-label {
	position: absolute;
	top: -5%;
	left: 0;
	margin-left: .5em;
	height: 100%;
	display: flex;
	align-items: center;
	font-size: 2rem;
	color: white;
}

.spacer {
	margin-block: 1.25em;
}

.main-content {
	display: flex;
	justify-content: center;
	position: relative;
	top: 5em;
}

.hero-title, .hero-link {
	z-index: 2;
}

.hero-title, .hero-link, .poster-img {
	margin-left: auto;
	margin-right: auto;
}

.hero-title {
	text-align: left;
	color: white;
	font-size: 1.5rem;
	margin-bottom: .5em;
	width: 20ch;
}

.hero-link {
	text-decoration: none;
	text-align: left;
	color: white;
	font-size: 1rem;
	background: var(--clr-primary);
	border-radius: 10px;
	padding: .3em;
	margin-bottom: .5em;
	height: max-content;
	width: max-content;
}

.poster-img {
	display: block;
	max-width: 15rem;
	border-radius: 10px;
	margin-bottom: 1em;
	z-index: -1;
}

// header media query
@media screen and (min-width:800px) {
	.nav-toggle-label {
		display: none;
	}

	header {
		display: grid;
		grid-template-columns: 1fr auto minmax(600px, 3fr) 1fr;
	}

	.logo {
		grid-column: 2 / span 1;
	}

	nav {
		all: unset;
		grid-column: 3 / span 1;
		display: flex;
		justify-content: flex-end;
		align-items: center;

		ul {
			display: flex;
		}

		li {
			margin-left: 3em;
			margin-bottom: 0;
		}

		a {
			opacity: 1;
		}
	}
}

// main content media query
@media screen and (min-width:800px) {
	.hero {
		display: grid;
		grid-template-columns: 1fr repeat(12, 1fr) 1fr;
		grid-template-rows: repeat(4, 1fr);
	}

	.hero::after {
	content: '';
	background: rgb(32, 32, 32, .5);
	position: absolute;
	inset: 0;
	z-index: 0;
}

	.hero-title, .hero-link {
		text-align: left;
		position: relative;
	}

	.hero-title, .hero-link, .poster-img {
		margin: 0;
	}

	.hero-title {
		position: relative;
		width: 20ch;
		font-size: 2rem;
		grid-column: 2 / span 8;
		grid-row: 2 / span 3;
	}

	.hero-link {
		grid-row: 3;
		grid-column: 2 / span 4;
		height: max-content;
		width: max-content;
		font-size: 1.25rem;

		&__2 {
			transform: translateY(125%);
		}
	}

	.poster-img {
		max-width: 12rem;
		text-align: center;
	}

	.poster-img-1 {
		grid-column: 7 / span 3;
		grid-row: 1 / span 4;
	}

	.poster-img-2 {
		grid-column: 9 / span 2;
		grid-row: 2 / span 4;
	}

	.poster-img-3 {
		grid-column: 11 / span 2;
		grid-row: 1 / span 5;
	}
	
}
